@import "src/assets/colors";

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 32px;
  @media screen and (max-width: 479px) {
    justify-content: start;
    box-sizing: border-box;
  }
}
.input {
  width: 480px;
  height: 56px;
  @media screen and (max-width: 479px) {
    width: 330px;
  }
  @media screen and (max-width: 426px) {
    width: 290px;
  }
}
.inp {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $primary;
  margin-top: 40px;
  width: 480px;
  line-height: 56px;
  &:hover {
    background-color: $primary-secondary;
  }
  @media screen and (max-width: 479px) {
    width: 330px;
    font-size: 20px;
  }
  @media screen and (max-width: 426px) {
    width: 290px;
  }
}
